.status-bar {
  display: flex;
  align-items: center;
  height: 70px;
  width: 100%;
  background: #fff;
  position: relative;
}

.status-bar__dropdown-content-wrapper {
  width: 100vw;
  padding: 50px 100px;
  background: #fff;
  z-index: 90;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 33px;
  position: relative;
  top: -1px;
}

.status-bar__filter {
  position: absolute;
  right: 100px;
  font-family: 'Inter', sans-serif;
  color: #808080;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  order: 4;
  align-items: center;
  text-align: right;
}

.status-bar__filter .status-bar__filter--icon {
  display: none;
}

.status-bar__filter .status-bar__filter--filter {
  display: none;
}

.status-bar__filter .status-bar__filter--all-number {
  margin-left: 10px;
}

.status-bar__dropdown-container {
  width: auto;
  height: 60px;
  display: inline-flex;
  align-items: center;
  position: relative;
  transform: translateY(100%);
  transition: 0s;
}

.status-bar__dropdown-container a {
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  color: #2E2E2E;
  text-decoration: none;
  width: 100%;
  height: 60px;
}

.status-bar__dropdown-container.active {
  transition: 0.3s;
  transform: translateY(0);
  transition-delay: 0;
}

.status-bar__link-1 {
  position: relative;
  display: inline-flex;
  height: 100%;
  align-items: center;
  margin-left: 100px;
  font-family: 'Inter', sans-serif;
  font-size: 14px;
  line-height: 17px;
}

.status-bar__link-1--link {
  color: #808080;
  text-decoration: none;
}

.status-bar__link-1--link:hover {
  color: #808080;
  text-decoration: none;
}

.status-bar__link-1--dropdown {
  display: flex;
  align-items: center;
  margin-left: 15px;
  transform: rotate(0);
  transition: all 0.15s ease-in 0s;
}

.status-bar__link-1--dropdown.transformed {
  margin-left: 15px;
  transform: rotate(-180deg);
}

.status-bar__link-1:hover {
  border-top: 29px solid transparent;
  padding-bottom: 28px;
  border-bottom: 1px solid #2E2E2E;
  color: #2E2E2E;
  text-decoration: none;
}

.status-bar__link-1.active .status-bar__link-1--link {
  color: #2E2E2E;
}

.status-bar__link-1.active .status-bar__link-1--dropdown.transformed svg path {
  stroke: #2E2E2E;
}

